.neon-title {
  color: #bfff00;
  text-shadow: 0 0 5px #80ff00, 0 0 5px #80ff00, 0 0 5px #80ff00;
}
.header-ment span {
  display: inline-block; // 각 글자가 개별 애니메이션 대상
  white-space: pre; // 띄어쓰기 보존
}
#header {
  height: 100vh;
  margin: 0 auto;
  padding: 5rem 0rem;
  @include layout(column, center, center);
  gap: 5rem;
  .header-title {
    @include layout(column, center, flex-start);
    h2 {
      font-weight: 900;
      font-size: 2rem;
      color: var(--color-teriary);
      @include txt-stroke(var(--color-teriary-line));
    }
    h1 {
      font-weight: 700;
      font-size: 3rem;
      color: var(--color-primary);
      @include txt-stroke();
      @include txt-shadow();
    }
  }
  .header-ment {
    @include layout(column, center, flex-start);
    padding: 1rem;
    line-height: 1.5;
    .mobile-text {
      display: block;
      @media (min-width: 1024px) {
        display: none;
      }
    }

    .desktop-text {
      display: none;
      @media (min-width: 1024px) {
        display: block;
      }
    }
  }
}

@include tablet {
  #header {
    height: 100vh;
    width: 70%;
    gap: 7rem;
    .header-title {
      h2 {
        font-size: 3.5rem;
      }
      h1 {
        font-size: 4.5rem;
      }
    }
    .header-ment {
      line-height: 1.5;
      font-size: 1.5rem;
    }
  }
}

@include desktop {
  #header {
    width: 55%;
    gap: 10rem;
    .header-title {
      h2 {
        font-size: 5rem;
      }
      h1 {
        font-size: 6rem;
      }
    }
    .header-ment {
      line-height: 1.7;
      font-size: 1.7rem;
    }
  }
}
